<template>
  <div>
    <div style="font-size: 16px; position: absolute; top: 12px; right: 40px">
      <el-link
        :underline="false"
        type="primary"
        style="font-size: 16px; cursor: pointer; margin-top: 10px"
        @click="add"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <div id="top">
      <div>
        <span>图书码</span>
        <el-input
          v-model="searchName"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>名称</span>
        <el-input
          v-model="searchName"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span>作者</span>
        <el-input
          v-model="searchName"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <span style="margin-left: 20px">荐书者</span>
        <el-input
          v-model="searchName"
          placeholder="请输入"
          size="mini"
          class="search"
        ></el-input>

        <el-button-group style="margin-left: 20px">
          <el-button type="primary" size="mini" @click="search">搜索</el-button>
          <el-button size="mini" @click="resetTable">重置</el-button>
        </el-button-group>
      </div>
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 130"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="图书码"
        align="center"
        prop="bookcode"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="图书名称" align="center" prop="name">
      </el-table-column>
      <el-table-column label="价格" align="center" width="100" prop="price">
      </el-table-column>

      <el-table-column
        label="作者"
        align="center"
        prop="author"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="荐书人"
        align="center"
        prop="sponsor"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="关联书单"
        align="center"
        prop="theme"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column
        label="喜爱度"
        width="100"
        align="center"
        prop="likes"
        show-overflow-tooltip
      ></el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <i
            class="el-icon-edit"
            style="margin-right: 20px"
            @click="handleEdit(row)"
          ></i>
          <i
            class="el-icon-delete"
            style="margin-right: 0"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 150, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="float: right; margin-top: 20px"
    >
    </el-pagination>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm" size="mini">
        <el-form-item label="图书码:" prop="bookcode">
          <el-input v-model="form.bookcode" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="名称:" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="价格:" prop="price">
          <el-input-number
            v-model="form.price"
            :min="0"
            label="价格"
          ></el-input-number>
        </el-form-item>

        <el-form-item label="作者:" prop="author">
          <el-input v-model="form.author" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="荐书人:" prop=" sponsor">
          <el-input v-model="form.sponsor" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="简介:" prop="profile">
          <el-input
            v-model="form.profile"
            autocomplete="off"
            type="textarea"
            :rows="2"
          ></el-input>
        </el-form-item>

        <el-form-item label="喜爱度:" prop="likes">
          <el-input-number
            v-model="form.likes"
            :min="0"
            label="喜爱度"
          ></el-input-number>
        </el-form-item>

        <el-form-item label="关联书单:" prop="theme">
          <el-select
            v-model="form.theme"
            multiple
            placeholder="请选择"
            style="width: 610px"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "mall_cop",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      loading: false,
      currentPage: 1, //当前页码
      pageSize: 20,
      total: 0,
      searchName: "",
      searchType: "",
      tableData: [
        {
          bookcode: "",
          name: "",
          price: "",
          author: "",
          sponsor: "",
          profile: "",
          likes: "",
          theme: "",
        },
      ],
      dialogFormVisible: false,
      dialogTitle: "新增",
      form: {
        bookcode: "",
        name: "",
        price: "",
        author: "",
        sponsor: "",
        profile: "",
        likes: "",
        theme: "",
      },
      rules: {
        bookcode: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 64,
            message: "长度在 1 到 64 个字符",
            trigger: "blur",
          },
        ],
        name: [
          { required: true, message: "请输入名称", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        price: [{ required: true, message: "请输入", trigger: "blur" }],
        author: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        sponsor: [
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        profile: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 300 个字符",
            trigger: "blur",
          },
        ],
      },
      options: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
    };
  },
  created() {},
  mounted() {},
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    resetForm() {
      this.$refs["ruleForm"].resetFields();
      this.form = {
        bookcode: "",
        name: "",
        price: "",
        author: "",
        sponsor: "",
        profile: "",
        likes: "",
        theme: "",
      };
      this.id = null;
      //this.getTableData();
    },

    add() {
      this.dialogTitle = "新增";
      this.dialogFormVisible = true;
    },
    handleEdit(row) {
      this.dialogTitle = "编辑";
      let data = JSON.parse(JSON.stringify(row));
      this.form = data;
      this.dialogFormVisible = true;
      this.id = data.id;
    },

    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        console.log(row);
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    handleSelectionChange(val) {
      console.log(val);
    },
    search() {},
  },
};
</script>
<style scoped>
#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 14px;
  color: #595959;
}
.search {
  width: 140px;
  margin-left: 10px;
  margin-right: 10px;
}
</style>
